.modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms linear, visibility 300ms linear;
}
.modal__backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 280px;
  background-color: $bgModal;
  @media screen and (min-width: 768px) {
    min-width: 568px;
  }
}

.modal__container {
  padding: 28px;
  @media screen and (min-width: 768px) {
    padding: 32px 40px;
  }
}

.modal__btn-close {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  margin-top: 12px;
  margin-right: 12px;
  border-color: transparent;
  background-color: transparent;
}

.close-btn-icon {
  width: 24px;
  height: 24px;
}

.modal__title {
  font-family: 'Seymour One', sans-serif;
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  color: $secondaryTextColor;
}

.modal__text {
  font-size: 14px;
  line-height: 1.42;
  color: $secondaryTextColor;
}

.modal__map {
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 199px;
  margin-top: 20px;
  @media screen and (min-width: 768px) {
    min-height: 260px;
    margin-top: 32px;
  }
}

.modal__input {
  width: 100%;
  max-width: 224px;
  padding: 8px 12px;
  background-color: $inputModal;
  border: 1px solid $inputBorder;
  border-radius: 10px;
  @media screen and (min-width: 768px) {
    max-width: 368px;
    padding: 11px 16px;
  }
}
