.contacts {
  background-color: $primaryBgColor;
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    text-align: start;
    color: $secondaryTextColor;
    @media screen and (min-width: 768px) {
      padding: 40px 24px 32px 24px;
      font-size: 14px;
    }
    @media screen and (min-width: 1200px) {
      padding: 40px 40px 32px 40px;
      font-size: 16px;
    }
  }
}

.contacts__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: center;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
  @media screen and (min-width: 1200px) {
    gap: 32px;
  }
}

.contacts__item {
  width: 280px;
  padding: 40px 32px 24px 32px;
  background-color: $bgWhite;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  @media screen and (min-width: 768px) {
    width: 224px;
  }
  @media screen and (min-width: 1200px) {
    width: 368px;
  }
}

.contacts__type {
  display: inline-block;
  text-transform: uppercase;
  padding-left: 23px;
  padding-right: 23px;
  border-radius: 4px;
  background-color: $accentColor;
  @media screen and (min-width: 780px) {
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 6px;
  }
}

.contacts__location {
  margin-top: 20px;
  @media screen and (min-width: 780px) {
    margin-top: 32px;
  }
}

.contacts__adress {
  margin-top: 8px;
  color: $contactsAdress;
  &:hover,
  &:focus {
    text-decoration: underline;
    cursor: url('../images/pointer.svg'), pointer;
  }
  @media screen and (min-width: 780px) {
    margin-top: 12px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 8px;
  }
}

.contacts__working-time {
  margin-top: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  border-top: 1px solid $contactsLine;
  border-bottom: 1px solid $contactsLine;
  @media screen and (min-width: 780px) {
    margin-top: 32px;
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

.contacts__working-hours {
  color: $contactsHours;
}

.contacts__communucation {
  margin-top: 24px;
  @media screen and (min-width: 780px) {
    margin-top: 32px;
  }
}

.contacts__phone,
.contacts__email {
  color: inherit;
}

.contacts__buttons {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 20px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    margin-top: 40px;
  }
  @media screen and (min-width: 768px) {
    margin-top: 60px;
  }
}

.contacts__main-button {
  min-width: 196px;
}

.contacts__secondary-button {
  min-width: 196px;
  background-color: transparent;
}
